.work-card {
  width: 640px;
  height: 832px;
  border-top: 1px solid var(--baseline-dark);
  border-right: 1px solid var(--baseline-dark);
  border-bottom: 1px solid var(--baseline-dark);
  box-sizing: border-box;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: var(--padding-45xl) var(--padding-12xl);
  gap: var(--gap-45xl);
  border-radius: 0;
  &:hover {
    .img {
      transform: scale(0.9);
    }

    .bg-img {
      transform: rotate(-10deg);
    }

    .button {
      .button-content {
        color: var(--baseline-dark);
        font-weight: 1000;
      }

      .icon1 {
        position: absolute;
        top: -50%;
        left: -50%;
        width: 200%;
        height: 200%;
        transform: translate(-50%, -50%) scale(10);
      }

      .icon {
        animation: slideInOut 1.75s ease-in-out infinite;
      }
    }
  }
}
.text {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-21xl);
}
.title {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.15px;
  line-height: 120%;
  font-weight: 500;
}
.content {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--h2-size);
  letter-spacing: -0.5px;
  line-height: 120%;
  text-transform: uppercase;
  font-weight: 400;
  font-family: inherit;
  height: 216px;
}
.content p {
  display: block;
  margin: 0;
}
.imgs,
.imgs-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.imgs {
  width: 360px;
  border-radius: var(--br-5xs);
  justify-content: flex-start;
  position: relative;
}
.imgs-wrapper {
  height: 240px;
  justify-content: center;
}
.img,
.bg-img {
  height: 240px;
  flex: 1;
  z-index: 1;
  position: absolute;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}

.bg-img {
  z-index: -1;
  transition: transform 1s ease;
}

.work-card .img {
  transition: transform 1s ease;
}
